import { BrowserModule } from '@angular/platform-browser';
import { NgModule, Component } from '@angular/core';
import { NgxSpinnerModule } from 'ngx-spinner';
import { FormsModule, ReactiveFormsModule }from'@angular/forms';
import { RouterModule, Routes }from '@angular/router';
import {HttpClientModule} from '@angular/common/http';

import { AppComponent } from './app.component';
import { from } from 'rxjs';
import { ButtonComponent } from './button/button.component';
import { SearchComponent } from './search/search.component';
import { ThreedComponent } from './threed/threed.component';
import { SlidershowComponent } from './slidershow/slidershow.component';
import { LoginComponent } from './login/login.component';
import { DashboradComponent } from './dashborad/dashborad.component';
import { EyeComponent } from './eye/eye.component';
import { AuthGuard } from '../app/auth.guard';
import { UserService } from './user.service';


const routes:Routes =[
  {path:"",component:LoginComponent},
  {path:"login",component:LoginComponent},
  {path:"dashborad",component:DashboradComponent,canActivate:[AuthGuard],children:[
    {path:"button",component:ButtonComponent},
{path:"search",component:SearchComponent},
{path:"3d",component:ThreedComponent},
{path:"slider",component:SlidershowComponent}
  ]},



]

@NgModule({
  declarations: [
    AppComponent,
    ButtonComponent,
    SearchComponent,
    ThreedComponent,
    SlidershowComponent,
    LoginComponent,
    DashboradComponent,
    EyeComponent,
    
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    NgxSpinnerModule,
    RouterModule.forRoot(routes)
    
  ],
  providers: [AuthGuard,UserService],
  bootstrap: [AppComponent]
})
export class AppModule { }
