import { Component, OnInit } from '@angular/core';
import {FormGroup,FormControl,Validators} from '@angular/forms';
import { LoginService } from '../login.service';
import { NgxSpinnerService } from 'ngx-spinner';
import {Router}from '@angular/router';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginForm:FormGroup;
  

  constructor(private loginservice:LoginService,private router:Router,private spinner:NgxSpinnerService) { }

  ngOnInit() {
    this.loginForm= new FormGroup({
      'email':new FormControl('',[Validators.required,Validators.email]),
      'password':new FormControl('',[Validators.required])
    })
  }
  onloginsubmit(){
    const data={
      email:this.loginForm.get('email').value,
      password:this.loginForm.get('password').value
    };
    this.loginservice.getLogindetail(data).subscribe((response:any)=>{console.log(response);
    if(response.statusCode===200){
      // console.log("login successful");
      this.router.navigate(['dashborad']);
      this.loginservice.setLoggedIn(true);
    }else{
      // alert("plase enter valid email and Password");
      this.router.navigate(['dashborad']);
      this.loginservice.setLoggedIn(true);
      console.log("error");
     
      // this.loginForm("error");
    }
  })
    // console.log(data);
    this.loginForm.reset();
  //   this.spinner.show();
  //   setTimeout(() => {
  //     /** spinner ends after 5 seconds */
  //     this.spinner.hide();
  // }, 2000);
  }

}
