import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
interface Mydata{
  email:string,
  success: boolean
}
interface isLoggedIn{
  status:boolean
}
@Injectable({
  providedIn: 'root'
})

export class UserService {

  constructor(private http:HttpClient) { }
  getSomeData(){
    return this.http.get<Mydata>('http://13.126.153.109:8001/v1/Users/login')
  }
  isLoggedIn():Observable<isLoggedIn>{
    return this.http.get<isLoggedIn>('http://13.126.153.109:8001/v1/Users/signup')
  }
}
